<template>
	<view class="wrapper" style="background-color: #000000;">


		<!-- 顶部区域 -->
		<view class="topBarCard">
			<!-- 搜索框 -->
			<view class="searchBox" @click="goSearchPage">
				<view class="searchButton">
					搜索
				</view>
			</view>
			<!--信息图标  -->
			<view class="messageBox" @click="goMessagePage">

			</view>
		</view>
		<view class="middleCard">
			<view class="timeDisplay">
				<view class="time">
					10
				</view>
				小时
				<view class="time">
					10
				</view>
				分后清空
			</view>
			<view class="timeOrHot">
				<view class="iconTime">

				</view>
				<view class="iconHot">

				</view>
			</view>
		</view>
		<view class="mainContent" v-for="(info,index) in cardInfoList" key="index">
			<backCard :cardInfo="info">
			</backCard>
		</view>
	</view>
</template>

<script>
	import backCard from '../../../components/Chen/backCard.vue';
	export default {
		data() {
			return {
				cardInfoList: [],
				userInfo: {},
			}
		},
		components: {
			backCard,
		},
		methods: {
			timestampToTime(timestamp) {
				let date = new Date(timestamp); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
				let M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
				let D = date.getDate() + ' ';
				let h = date.getHours() + ':';
				let m = date.getMinutes();
				return {
					date: M + D,
					time: h + m
				};
			},
			// 获取数据
			getCardInfo() {
				var that = this;
				uni.request({
					url: that.$serverUrl + '/article/queryArticles',
					method: 'POST',
					data: {
						page: 1,
						pageSize: '10',
						userId: that.userId,
						queryType: '0',
						orderType: '0',
						selectedTag: ""
					},
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					success: res => {
						console.log('success');
						console.log(res);
						var cardInfo = {};
						for (var i = 0; i < res.data.data.rows.length; ++i) {
							cardInfo = {
								"UserImg": res.data.data.rows.faceImg,
								"userName": res.data.data.rows[i].nickname,
								"contentText": res.data.data.rows[i].articleContent,
								"timeDisplay": this.timestampToTime(res.data.data.rows[i].createDate).date + "" + this.timestampToTime(res.data
									.data.rows[i].createDate).time,
								"isAnswered": 1,//res.data.data.rows[i].commentNum,
								"answerText": "写代码写代码哈哈哈",
								"answerName": "33",
								"answerImg": "",
							};
							this.cardInfoList.push(cardInfo);
						}
					},
					fail: res => {
						console.log("failed");
					}
				});
			},

		},
		mounted() {
			this.getCardInfo()
		},
	}
</script>

<style>
	/* 设置顶部框 */
	.topBarCard {
		position: fixed;
		width: 100%;
		height: 68px;
		display: flex;
		align-content: space-between;
		padding-top: 12px;
	}

	/* 搜索框样式 */
	.topBarCard>.searchBox {
		width: 250px;
		height: 34px;
		background: #FFFFFF;
		border-radius: 15px;
		margin-left: 10px;
	}

	/* 搜索图标 */
	.topBarCard>.searchCard>.searchButton {
		margin: 0 auto;
	}

	/* 信息图标样式 */
	.topBarCard>.messageBox {
		width: 87px;
		height: 34px;
		background: #7D7D7D;
		border-radius: 34px;
		margin-left: 20px;
	}

	.middleCard {
		display: flex;
		align-content: space-between;
		width: 90%;
		height: 50px;
		margin: 0 auto;
	}

	.middleCard>.timeDisplay {
		width: 50%;
		display: flex;
		height: 50px;
		/* 文字样式 */
		font-size: 11px;
		font-weight: bold;
		color: #FFFFFF;
		line-height: 38px;
		opacity: 0.8;
	}

	.middleCard>.timeDisplay>.time {
		margin-bottom: 0px;
		width: 30%;
		height: 30px;
		background-image: linear-gradient(#000000, #626262);
		/* 文字样式 */
		font-size: 30px;
		font-weight: bold;
		color: #FFFFFF;
		line-height: 30px;
		opacity: 0.8;

	}

	.timeOrHot {
		width: 80px;
		display: flex;
		height: 30px;
		margin-left: 65px;
	}

	.middleCard>.timeOrHot>.iconTime {

		background-color: #DBA503;
		width: 40px;
		height: 100%;
	}

	.middleCard>.timeOrHot>.iconHot {
		background-color: #636363;
		width: 40px;
		height: 100%;
	}
</style>
